.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

input#header-search {
  margin-top: 35px;
  padding: 5px 10px;
  width: 135px;
  height: 20px;
  padding-left: 5px;
  font-size: 13px;
  width: 500px;
}

.go {
  margin-top: 10px;
  background-color: #4caf50;
  border: none;
  color: white;
  padding: 8px 16px;
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
}
